import React, { useEffect, useState } from "react";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import { login } from "masto";

function forEach(ai, fn) {
  return ai.next().then(function (r) {
    if (!r.done) {
      fn(r);
      return forEach(ai, fn);
    }
  });
}

function fetchAll(ai) {
  let res = [];
  const fn = (v) => {
    res = res.concat(v.value);
  };

  return forEach(ai, fn).then(() => res);
}

function ClientTest() {
  const [key, setKey] = useState(null);

  const doit = () => {
    if (!key) return;

    login({
      url: "https://hachyderm.io",
      accessToken: key,
    }).then((masto) => {
      masto.accounts.verifyCredentials().then((me) => {
        const cnt = me.followingCount;
        let following = [];
        let lists = [];
        fetchAll(masto.accounts.getFollowingIterable(me.id))
          .then((x) => (following = x))
          .then(() => masto.lists.fetchAll())
          .then((x) => (lists = x))
          .then(() => {
            const proms = lists.map((x) =>
              fetchAll(masto.lists.getAccountIterator(x.id))
            );
            return Promise.all(proms);
          })
          .then((listaccts) => {
            // Build up lists for each follower
            following.forEach((x) => (x.lists = []));
            // A map for easy lookup
            const followerMap = {};
            following.forEach((x) => (followerMap[x.id] = x));
            // And, for each list
            lists.forEach((list, idx) => {
              const accts = listaccts[idx];
              accts.forEach((acct) => {
                const fol = followerMap[acct.id];
                fol.lists.push(list.id);
              });
            });
            return {
              following: following,
              lists: lists,
            };
          });
      });
    });
  };

  useEffect(() => {
    doit();
  }, [key]);

  return (
    <div>
      Key:
      <TextField
        value={key}
        onChange={(evt) => setKey(evt.target.value)}
        label="Key"
      />
      <Button onClick={() => doit()}>Go</Button>
    </div>
  );
}

export default ClientTest;
